            <h2>请点击任意处继续...</h2>
        </div>
    </transition>

    <!-- 加载页面 -->
    <transition name="preload">
          <div v-if="preload" class="preload">
              <h1 class="to-my-love" v-if="love">To My Love</h1>
              <h1 class="ms-lds" v-if="lds">Ms.LIDSHU</h1>
              <div class="loading">
            <span>LOADING</span>
            <div class="loading-point"></div>
        </div>
            <img src="./images/new.gif" width="400" draggable=false>
            <div class="zhedang"></div>
            <p class="author">Author: <span>马新治</span>😎</p>
          </div>
      </transition>

      <!-- 背景 -->
      <div class="background">
      </div>

      <!-- 正题内容 -->
      <transition name="content">
          <div class="content" v-if="content">
              <!-- 下雪特效 -->
          <div class="snowContent">
              <div v-for="n in 99" class="snowball" 
         :style="{ 
             backgroundColor: '#F5F5F5', 
             opacity: randomNumberForSnow(0.5,1)
             }" :key='n'>
      </div>
      </div>
      <!-- 雪人 -->
      <img src="./images/envelope.png" class="envelope" @click="heartWord=true">
      <div v-if="heartWord" class="heartWord" @click.self.stop="heartWord=false">
          <img src="./images/heartWord.png" class="heartWord1">
          <img src="./images/close.png" class="heartWord2" @click="heartWord=false">
      </div>
      <div class="snowmanContent">
        <img class="snowman" src="./images/snowman.png" width="150" draggable=false>
        <div class="chatBox">
            <p>{{ speakWord }}</p>
        </div>
      </div>

      </div>
      </transition>

      <!-- 音乐 -->
      <div>
          <audio id="audio" volume=0.1 loop="loop">
              <source src="./music/jingle-bells.mp3" draggable=false>
          </audio>
      </div>
</template>

<script>
import { gsap } from "gsap/dist/gsap";
import { ref } from 'vue'
var _ = require('lodash');
export default {
    setup() {
        const speakWord = ref('')
        return {
            speakWord
        } 
    },

     data() {
        return {
            clickPage: true,
            preload: false,
            content: false,
            love: true,
            lds: false,
            heartWord: false,
            words: ['哎哟，屁股好痛~(定睛一看)', '圣诞节快乐 ~ 宝贝❤️🎅🎁', '这是我们在一起后的第二个圣诞节哦😋', '让我们开开心心地度过2021的最后几天~😆'],
            randomWords: [["I don't know where can buy the 99 of Roses...", "So I made that 99 of Snowflakes", "And that 999 lines of Codes"], 
                          ["真诚之中，与你相识相知。", "灵犀之间，与你朝夕相伴。"],
                          ["知道我在做什么吗？给你五个选择：", "A：想你；", "B：很想你；", "C：非常想你；", "D：想你想得无法入睡；", "E：以上皆是。"],
                          ["如果世界只剩十分钟，我会和你一同回忆走过的风雨", "如果只剩三分钟，我会吻你","如果只剩一分钟，我会说次我爱你！"], 
                          ["鱼对水说你看不到我的眼泪，因为我在水里。", "水说我能感觉到你的眼泪，因为你在我心里。"], 
                          ["看到你，我怕触电", "看不到你，我需要充电", "如果没有你，我会断电。"],
                          ["无论何时，不要忘记想念我", "想念我的时候，不要忘记我也在想念你。"],
                          ["I don't know where can buy the 99 of Roses...", "So I made that 99 of Snowflakes", "And that 999 lines of Codes"],
                          ["随时可以回来看我哦~😏"],
                          ["人生的路很长很长让我陪你一起走吧", "我愿与你搭建一个属于我们自己的天空。"],
                          ["或许我没有太阳般狂热的爱，也没有流水般绵长的情", "只知道不断地爱你、无所不能地为你……"],
                          ["爱你，只愿深情地看着你；","想你，梦全是你；", "爱你，眼睛永远跟着你。", "我愿意每时每刻都把你放在心里，每时每刻都期待着你的幸福。"],
                          ["I want to grow old with you."],
                          ["Your name is my favorite English word for coding a example name of programming."]
                          ],
            // 展示语句
            showRandomWords: null
        }
    },

    methods: {
        // 点击第一个页面，就自动播放音乐，然后为接下来的页面还有字做定时切换。
        playMusic() {
            this.clickPage = false
            this.preload = true
            let vm = this
            const audio = document.querySelector('#audio')
            setTimeout(() => {
                audio.play()
                
            }, 600)
            setTimeout(() => {
                 vm.preload = false
                 vm.content = true
                 vm.checkBalls()
                 vm.moveSnowMan()
                 vm.changeWord()
            }, 15000)
            setTimeout(() => {
                this.love = false
                this.lds = true
            }, 8000);
        },

        randomNumber(min, max) {
            return Math.floor((max - min) * Math.random() + min)
        },
        moveSnow(el) {
            gsap.to(el, {y: this.randomNumber(900, 1800),x:this.randomNumber(-300, 300), duration:this.randomNumber(10, 18), delay: this.randomNumber(0.5,10), repeat: -1})
        },
        randomNumberForSnow(min, max) {
            return (max - min) * Math.random() + min
        },
        // 为每个雪球的高都设置为宽的长度。然后开始无限移动的动画。
        checkBalls() {
            setTimeout(() => {
                const snow = document.querySelectorAll('.content .snowContent .snowball')
                snow.forEach((el) => {
                    el.style.width = el.style.height = this.randomNumber(10, 15) + 'px'
                    console.log(el.style.width, el.style.height)
                    this.moveSnow(el)
                })
            }, 950);
        },

        // 让雪人掉下来的动作
        downMan(el) {
                gsap.to(el, {y: 670, duration:2, delay: 2, ease: 'bounce.out'})
        },


        // 定时锁定雪人，然后运行动画函数
        moveSnowMan() {
            setTimeout(() => {
                const snowMan = document.querySelector('.content .snowmanContent')
                this.downMan(snowMan)
            }, 950)
        },

        //定时让雪人切换语句的函数
        changeWord() {
            setTimeout(() => {
                const chatBox = document.querySelector('.content .snowmanContent .chatBox')
                chatBox.style.opacity = '1'
                this.changeWord1()
            }, 5500);
        },

        changeWord1() {
            setTimeout(() => {
                this.speakWord = this.words[1]
                this.changeWord2()
            }, 3000);
        },

        changeWord2() {
            setTimeout(() => {
                this.speakWord = this.words[2]
                this.changeWord3()
            }, 3500);
        },

        changeWord3() {
            setTimeout(() => {
                this.speakWord = this.words[3]
                this.makeHidden()
            }, 3500)
        },

        // 进场台词的隐藏对话框
        makeHidden() {
            setTimeout(() => {
                const chatBox = document.querySelector('.content .snowmanContent .chatBox')
                chatBox.style.visibility = 'hidden'
                setTimeout(() => {
                    this.showRandomJuZi()
                }, 43000)
            }, 2500);
        },

        makeHiddenForRandom() {
            const chatBox = document.querySelector('.content .snowmanContent .chatBox')
                chatBox.style.visibility = 'hidden'
                this.showRandomJuZi()
        },
        // 展示随机语句的函数
        showRandomJuZi() {
            setTimeout(() => {
                let length = this.randomWords.length
                let randomLength = Math.floor(_.random(0, length-1))
                this.showRandomWords = this.randomWords[randomLength]
                this.speakWord = this.showRandomWords[0]
                const chatBox = document.querySelector('.content .snowmanContent .chatBox')
                chatBox.style.visibility = 'visible'
                this.randomWords1()
            },5000)
        },

        //接着展示该随机索引里的数组里的句子
        randomWords1() {
            setTimeout(() => {
                if(1 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[1]
                    this.randomWords2()
                }
            }, 3000)
        },

        randomWords2() {
            setTimeout(() => {
                if(2 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[2]
                    this.randomWords3()
                }
            }, 3000);
        },

        randomWords3() {
            setTimeout(() => {
                if(3 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[3]
                    this.randomWords4()
                }
            }, 3000);
        },

        randomWords4() {
            setTimeout(() => {
                if(4 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[4]
                    this.randomWords5()
                }
            }, 3000);
        },

        randomWords5() {
            setTimeout(() => {
                if(5 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[5]
                    this.randomWords6()
                }
            }, 3000);
        },

        randomWords6() {
            setTimeout(() => {
                if(6 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[6]
                    this.randomWords7()
                }
            }, 3000);
        },

        randomWords7() {
            setTimeout(() => {
                if(7 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[7]
                    this.randomWords8()
                }
            }, 3000);
        },

        randomWords8() {
            setTimeout(() => {
                if(8 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[8]
                    this.randomWords9()
                }
            }, 3000);
        },

        randomWords9() {
            setTimeout(() => {
                if(9 === this.showRandomWords.length) {
                    this.makeHiddenForRandom()
                } else {
                    this.speakWord = this.showRandomWords[9]
                    this.randomWords10()
                }
            }, 3000);
        },
    },

    mounted() {
        //刚开始先让台词的准备台词数组的索引为0
        this.speakWord = this.words[0]
        
    }
}

</script>

<style>
